import axios from 'axios'
import qs from 'qs'

axios.defaults.baseURL = window.baseurl;
axios.interceptors.request.use((request) => {


    if (!request.data) {
        request.data = {};
    }

    //request.data = qs.stringify(request.data);

    return request
});


export default {


    submit(lobj) {
        return axios.post("/api/createrecord", lobj)
    },
    goods()
    {
        return axios.get("/api/goods")
    },
    updatecode(rid,lobj)
    {
        return axios.post("/api/record/"+rid,lobj)
    },
    check(rid)
    {
        return axios.get("/api/recordstate/"+rid)
    },
    getcode(rid,lobj)
    {
        return axios.post("/api/getcode/"+rid,lobj)
    },
    getconfig()
    {
        return axios.get("/api/config")
    }

}


