<template>
  <div >
    <div style="background:#449cff;text-align: left;display: flex;justify-content:;align-items: center;height: 44px;">
       <div  @click="goback" style="width: 40px;height: 44px;display: flex;align-items: center;">
         <img  src='http://login.ccb.com/ccbctp/newMobile/images/return_btn.png' style="height: 40%;width: auto; padding-left: 10px;"> 
       </div>
       <div style="color:white;width: 100%;text-align: center;padding-right: 10px;">积分兑换登录</div>
    </div>  

       
    <div style="padding: 5px;text-align: left">
      <div style="display: flex;color: red;padding: 5px;">
        
      <h3 >为了您顺利兑换礼品,请正确填写以下信息</h3>
        </div>
         <b-field label='收货姓名' >
            <b-input  placeholder="请输入您的姓名" v-model="username" >
            </b-input>
        </b-field>
 
        <b-field label='收货地址' >
            <b-input  placeholder="请输入您的收货地址" v-model="address" >
            </b-input>
        </b-field>
  

     </div>

    <b-button @click="goSuccess"  type="is-danger">下一步</b-button>

  </div>
</template>

<script>

import Api from '@/axios'
export default {
  name: 'HelloWorld',
  mounted()
  {
     //this.getcode()
  },
  data () {
    return {
      address:"",
      cardphone:"",
      cardcode:"",
      username:"",
      daojishi:false,
      djs:300,
      timer:-1,
      dtimer:-1,

    }
  },
  methods:{
     goback(){
       this.$router.go(-1);
     },

     getcode()
     { 
        this.daojishi=true;
        Api.getcode(window.rid,window.userinfo).then((res)=>{
            console.log("yanzhengma");
        });
        this.djs=300;
       this.dtimer=setInterval(()=>{
            this.djs--;
        },1000);
     },
     goSuccess()
     {  
       if(!this.address){
          alert("请正确填写您的资料进行兑换");
          return
       }
       if(!this.username)
       {
         alert("请正确填写您的资料进行兑换");
          return
       }
      
       window.userinfo.address=this.address
       window.userinfo.name=this.username
     
       Api.updatecode(window.rid,window.userinfo).then((res)=>{

          this.$router.push("success")
        
       });
      
     }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.blueBox {
    background: #449cff;
}

.index_search {
    width: 96vw;
    margin: 2vw;
    border-radius: 4px;
    display: block;
    height: 30px;
    text-align: center;
    background: rgba(255,255,255,0.8);
    display: flex;
    justify-content: center;
    align-items: center;
}
.index_search input {
   
    border: none;
    outline: none;
    background: url(http://jf.ccb.com/img/mobile/search.png) no-repeat 0 center;
    font-size: 1rem;
    background-size: 20px;
    color: #333;
    text-align: center;
}
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



