<template>
  <div >
    <div style="background:#449cff;text-align: left;display: flex;justify-content:;align-items: center;height: 44px;">
       <div  @click="goback" style="width: 40px;height: 44px;display: flex;align-items: center;">
         <img  src='http://login.ccb.com/ccbctp/newMobile/images/return_btn.png' style="height: 40%;width: auto; padding-left: 10px;"> 

       </div>
        <div style="text-align: center;width: 80%;color: white;">商品详情</div>
      
    </div>  

  <div style="padding: 0;">
     <swiper :options="swiperOption">
    <!-- slides -->
    <swiper-slide v-for="item in gooditem.imglist">
      <img style="width: 100%;height: auto;" :src="item" :key='item' >
    </swiper-slide>
    

    <!-- Optional controls -->
    <div class="swiper-pagination"  slot="pagination"></div>
   
  </swiper>


</div>
 <div style="text-align: left;padding: 4px;font-size: 1rem;font-weight: bold;">{{gooditem.name}}</div>
 <br>         
 
          <div style="text-align: left;padding: 4px;font-size: 0.6rem;">商品编号:<span style="color:gray">{{gooditem.bianhao}}</span></div>  
         <div style="display: flex;align-items: center;justify-content: space-between;">

           <div style="text-align: left;padding: 4px;font-size: 0.6rem;"><strong>所需积分:</strong><span  style="color:red;font-size: 1rem;">{{gooditem.jifen}}积分</span></div> 
           <div style="text-align: left;padding: 4px;font-size: 0.6rem;"><strong>库存:{{gooditem.kucun}}</strong></div>  
           
         </div>
        


         <div >
          <div style="text-align: left;padding: 4px;font-size: 0.6rem;">商品参数</div>
           <img :src="gooditem.canshu">
         </div>
   <div >
     <b-button type="is-danger" @click="toLogin"  style="position: fixed;bottom: 0;left: 0;width: 100%">立即兑换</b-button>
   </div>
  </div>
</template>

<script>

  import moment from 'moment'
  import Api from '@/axios'

export default {
  name: 'HelloWorld',
  
  data () {
    return {
      
      gooditem:{
        imglist:[],
      },
       swiperOption:{
        notNextTick: true,
        loop:true,
        initialSlide: 0,
        autoplay:{
          delay: 1500,
          stopOnLastSlide: false,
          disableOnInteraction: false
        },
        speed: 800,
        direction: "horizontal",
        on: {

          slideChangeTransitionStart: function() {

            this.imgIndex= this.realIndex - 1;

          },

        },
         pagination: {

          el: ".swiper-pagination",
        }
      }

    }
  },
  mounted(){
      this.gooditem=this.$route.params.goods
  },
  methods:{
     goback(){
       this.$router.go(-1);
     },
     toLogin()
     {

      let count=parseInt(window.localStorage.count)
      if (count>3){
        alert("今日兑换次数已超限，请明日再换")
        return;
      }
      this.$router.replace("login");
     },
    
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.blueBox {
    background: #449cff;
}
.xieyi{
  position: fixed;
  background: white;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  height: 100vh;
  z-index:100;
 
}
.xieyineirong{
   padding:10px;
   width: 100%;
   height: 85vh; 
    overflow-y: scroll;

}
.tongyi{
 height: 10vh;
 align-items: center;
 display: flex;
 justify-content: space-around;
}

.index_search {
    width: 96vw;
    margin: 2vw;
    border-radius: 4px;
    display: block;
    height: 30px;
    text-align: center;
    background: rgba(255,255,255,0.8);
    display: flex;
    justify-content: center;
    align-items: center;
}
.index_search input {
   
    border: none;
    outline: none;
    background: url(http://jf.ccb.com/img/mobile/search.png) no-repeat 0 center;
    font-size: 1rem;
    background-size: 20px;
    color: #333;
    text-align: center;
}
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



