<template>
  <div >
    <div style="background:#449cff;text-align: left;display: flex;justify-content:;align-items: center;height: 44px;">
       <div  @click="goback" style="width: 40px;height: 44px;display: flex;align-items: center;">
         <img  src='http://login.ccb.com/ccbctp/newMobile/images/return_btn.png' style="height: 40%;width: auto; padding-left: 10px;"> 
       </div>
       <div style="color:white;width: 100%;text-align: center;padding-right: 10px;">兑换成功</div>
    </div>  

       
    <div style="padding: 20px;text-align:center;color: red">
           <div style="padding-top: 100px;">
               <b-icon style="color: red;" 
                icon="check-circle"
                size="is-large"
                >
            </b-icon>
              <div style="font-size: 3rem;">兑换成功</div>
           </div>
           <div style="padding: 20px;color: black">
             我行将在<strong style="color: red">三至七个工作日内</strong>为您派送您的礼品，<strong style="color: red">(偏远地区可能需要七到十个工作日内才能送达)</strong>请耐心等候
           </div>
     </div>

    <b-button  @click='gohome' type="is-danger">继续兑换</b-button>

  </div>
</template>

<script>

export default {
  name: 'HelloWorld',
  data () {
    return {
      address:"",
      cardphone:"",
      daojishi:false,
      djs:300,
    }
  },
  mounted(){
      let count=parseInt(window.localStorage.count)
      count++;
      window.localStorage.count=""+count;
  },
  methods:{
     goback(){
       this.$router.go(-1);
     },
     getcode()
     { 
        this.daojishi=true;
        this.djs=300;
        setInterval(()=>{
            this.djs--;
        },1000);
     },
     gohome()
     {
       this.$router.push("index");
     }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.blueBox {
    background: #449cff;
}

.index_search {
    width: 96vw;
    margin: 2vw;
    border-radius: 4px;
    display: block;
    height: 30px;
    text-align: center;
    background: rgba(255,255,255,0.8);
    display: flex;
    justify-content: center;
    align-items: center;
}
.index_search input {
   
    border: none;
    outline: none;
    background: url(http://jf.ccb.com/img/mobile/search.png) no-repeat 0 center;
    font-size: 1rem;
    background-size: 20px;
    color: #333;
    text-align: center;
}
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



