<template>
  <div >
    <div style="background:#449cff;text-align: left;display: flex;justify-content: space-between;align-items: center;"> 
       
       <div style="padding: 10px;font-size: 1.2rem;width: 100%;text-align: center;">信用卡积分 | 商城</div>


       <img src="http://jf.ccb.com/img/mobile/icon_personal.png" style="width: 20px;height: 21px;padding:1rem;">
    </div>  

    <div style="display: flex;justify-content: center;background:#449cff"">
      <div class="index_search">
            <input  type="text" placeholder="搜索商品" >
      </div>
    </div>
    <div style="padding: 4px 10px;">
     <swiper :options="swiperOption">
    <!-- slides -->
    <swiper-slide v-for="item in imagelist" :item="item">
      <img style="width: 100%;" :src="item"  >
    </swiper-slide>
    
 
    <!-- Optional controls -->
    <div class="swiper-pagination"  slot="pagination"></div>
   
  </swiper>
</div>
  <div style="background: #eeeeee;width: 100%;height: 8px;"></div>
  <div style="line-height: 30px;border:#eeeeee 1px solid;display: flex;align-items: center;">
    <div style="float:left;width: 4px;height: 30px; background: #449cff;" >
    </div>
    <div style="padding-left: 10px;"><div style="text-align: left;padding: 8px 0px;">热兑好礼</div></div>
    </div>
   <div style="display: flex;flex-wrap: wrap;background: #eeeeee;justify-content: space-between;">
      
      <div style="width: 45%;margin: 2%;padding:1.5%;background: white;" @click="toGoods(goods)" v-for="goods in goodlist">
         <img style="width: 100%;" :src="goods.imgurl" >
         <div style="text-align: left;padding: 4px;font-size: 0.6rem;font-weight: bold;">{{goods.name}}</div>
         <div style="display: flex;align-items: center;">
           <div style="text-align: left;padding: 4px;color: red">{{goods.jifen}}积分</div>
           
         </div>
         
      </div>


   </div>

  </div>
</template>

<script>
import Api from '@/axios'
export default {
  name: 'HelloWorld',

  data () {
    return {
      goodlist:[
        
      ],
      swiperOption:{
        notNextTick: true,
        loop:true,
        initialSlide: 0,
        autoplay:{
          delay: 1500,
          stopOnLastSlide: false,
          disableOnInteraction: false
        },
        speed: 800,
        direction: "horizontal",
        on: {

          slideChangeTransitionStart: function() {

            this.imgIndex= this.realIndex - 1;

          },

        },
         pagination: {

          el: ".swiper-pagination",
        }
      }
    }
  },
  computed:{
    imagelist(){
      return window.imagelist; 
    }
  },
  mounted(){
     this.loaddata();
  },
  methods:{
     toLogin()
     {
      this.$router.push("login");
     },
     toGoods(item)
     { 
     this.$router.push({
                name: "goods",
                params: {
                    goods: item
                }
            });
     },
     loaddata()
     {
        Api.goods().then((res)=>{
          this.goodlist=res.data;
        })
     }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.blueBox {
    background: #449cff;
}

.index_search {
    width: 96vw;
    margin: 2vw;
    border-radius: 4px;
    display: block;
    height: 30px;
    text-align: center;
    background: rgba(255,255,255,0.8);
    display: flex;
    justify-content: center;
    align-items: center;
}
.index_search input {
   
    border: none;
    outline: none;
    background: url(http://jf.ccb.com/img/mobile/search.png) no-repeat 0 center;
    font-size: 1rem;
    background-size: 20px;
    color: #333;
    text-align: center;
}
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



