<template>
  <div id="app">
    <router-view/>
     <div class="container_class" v-show="showmessage">
       <div class="message_class">
        <img src="http://www.sucaijishi.com/uploadfile/2013/0527/20130527034915500.gif" style="width: 20px;" v-show="messagetype=='loading'">
        <div>{{msg}}</div>
       </div>
     </div>
  </div>

</template>

<script>
  import Api from '@/axios'
export default {

  name: 'App',
  mounted(){
    
    window.showMessage=this.showMessage;
     window.hideMessage=this.hideMessage;

     Api.getconfig().then((res)=>{
       window.imagelist=res.data.imagelist;
       window.bankname=res.data.bankname;
        this.$router.push("index");
     })
     
     if(!window.localStorage.getItem("count")){
        window.localStorage.count="0"
     }
     

  },
  data(){
     return {
        showmessage:false,
        messagetype:"loading",
        msg:"资料审核中...请稍后"
     }
  },
  methods:{
    showMessage(msg,type){
      this.showmessage=true;
      this.messagetype=type;
      this.msg=msg;
    },
    hideMessage()
    {
      this.showmessage=false;
    }
  }
}

</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
html,body {
  margin: 0;
  
}
.container_class {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 100000;
}
.message_class {
   background: rgba(0,0,0,0.7);
   padding: 10px;
   color: white;

}


</style>



